<script>
	import {queryCarousels,helpData,listDatasd,oilDatasd} from '../../api/info';
	
	export default{
		name:'sss',
		data(){
			return{
				imgUrlBanner:'',
				tabArr:[],
				num :0,
				currentpage:1,
				total:0,
				pageNum:1,
				listData:[],
				channelpara:'',
				total1:0,
				currentpage1:1,
				pageNum1:1,
				tableList:[]
			}
		},
		methods:{
//			imgMove(){
//				
//			},
			rollImg($event){
				 var zoom=parseInt(this.$refs.imgs.style.zoom)||100;
				  zoom+=$event.wheelDelta/12;
				  if (zoom>0) this.$refs.imgs.style.zoom=zoom+'%';
				   return false;
			},
			getInfo(){
				this.$router.push({path:'/'})
			},
			goweb(param){
				var uuid = window.localStorage.setItem('uuid',param)
 			 	this.$router.push({
 			 		path:'/active?uuid='+param,query:{name:'SDZL'}
 			 	})
			},
			pageFn(sty){
				this.pageNum = sty
				var parameterData ={
					'channelMDesc':'SDZL',
					 "channelName": this.channelpara,
					 "pageNum": this.pageNum,
					 "pageSize":10,
				}
				listDatasd(parameterData).then((res)=>{
					this.listData=res.datas.infoList;
				});
			},
			pageFn1(sty){
				this.pageNum1 = sty
				var parameterData1 ={
					 "pageNum": this.pageNum1,
					 "pageSize":10
				}
				oilDatasd(parameterData1).then(res =>{
					
					this.total1 = res.total
					this.tableList = res.datas
				})
			},
			getData(para,index){
				this.num = index;
				this.currentpage=1;
				this.currentpage1=1;
				if(index ===0 || index ===1){					
					this.channelpara = para;
					this.pageNum =1;
					var parameterData ={
						'channelMDesc':'SDZL',
						 "channelName": para,
						 "pageNum": 1,
						 "pageSize":10,
					}
					listDatasd(parameterData).then((res)=>{
						this.listData=res.datas.infoList;
						this.total = res.total
					});
				}else if(index ===2){
					this.pageNum1 =1;
					var parameterData1 ={
						 "pageNum": 1,
						 "pageSize":10
					}
					oilDatasd(parameterData1).then(res =>{
						this.total1 = res.total
						this.tableList = res.datas
					})
				}
				
			},
			initData(){
				var param = {
					pageNum:1,
					pageSize:1,
					channelMDesc:'SDZL'
				}
				helpData(param).then(res => {
					this.tabArr = res.datas;
					var param = res.datas[0].channelName
					var parameterData ={
						'channelMDesc':'SDZL',
						"channelName":param ,
						"pageNum": 1,
						"pageSize":10,
					}
					listDatasd(parameterData).then((res)=>{
						
						this.listData=res.datas.infoList;
						this.total = res.total
					});
				})
			},
			getBanner(){
				queryCarousels({typeCode:"WEB",pageCode:"zx_index_barner",pageNum:1,pageSize:10}).then((res)=>{
					this.imgUrlBanner = res.datas[0].imageUrl
				});
			}
		},
		directives:{
			drag: {bind:function (el, binding) {
		            let oDiv = el;   //
		            
		            oDiv.onmousedown = function (e) {
		             //
		                let disX = e.clientX - oDiv.offsetLeft;
		                let disY = e.clientY - oDiv.offsetTop;
		                
		                document.onmousemove = function (e) {
//		                	console.log(-(document.documentElement.offsetHeight - oDiv.offsetHeight))
		                  // 
		                    let l = e.clientX - disX;
		                    let t = e.clientY - disY;
		                   
		                    if(l>=0){
		                    	l=0;
		                    }else if(l<= -(oDiv.offsetWidth - 100)){
		                    	l= -(oDiv.offsetWidth - 100)
		                    }
		                    if(t>=0){
		                    	t=0
		                    }else if(t <= -(oDiv.offsetHeight - 100)){
		                    	t = -(oDiv.offsetHeight - 100)
		                    	
		                    }
		                  //  
		                    oDiv.style.left = l + 'px';
		                    oDiv.style.top = t + 'px';
//		                    console.log(oDiv.getBoundingClientRect().left + )

		                     //
//		                    binding.value({x:e.pageX,y:e.pageY})
		                };
		                document.onmouseup = function (e) {
		                
		                    document.onmousemove = null;
		                    document.onmouseup = null;
		                 };
		            };
		        }
		    }
		},
		
		mounted(){
			$('.bk-title1 li').click(function(){
				var $index = $(this).index()
				$('.inf-search-cont').css({display:'none'})
				$('.bk-title1 li a').removeClass('cur')
				$(this).children('a').addClass('cur')
				$('.inf-search-cont').eq( $index).css({display:'block'})
			})
			
			$('.oilMap li').click(function(){
				var $index = $(this).index()
				$('.tabMap').css({display:'none'})
				$('.oilMap li a').removeClass('cur')
				$(this).children('a').addClass('cur')
				$('.tabMap').eq($index).css({display:'block'})
			})
			
			
			
		},
		created(){
			this.getBanner()
			this.initData()
		}
	}
</script>

